import api from '@/api/index.js';
import rightGoods from './rightGoods.vue';
import crumb from "./crumb.vue";
import mSelect from './select';
import goodsItem from './goodsItem';
export default {
  data: function data() {
    return {
      list: [],
      area: []
    };
  },
  created: function created() {
    var _this = this;

    api.getFoodList().then(function (res) {
      _this.list = res.data.data;
    });
    api.getFoodPositonList().then(function (res) {
      var data = res.data + ']}';
      _this.area = JSON.parse(data).data;
    });
  },
  components: {
    crumb: crumb,
    mSelect: mSelect,
    goodsItem: goodsItem,
    rightGoods: rightGoods
  }
};