import api from '@/api/index.js';
import mCity from '@/components/changeCity/city.vue';
import hotCity from '@/components/changeCity/hotCity.vue';
import selectCity from '@/components/changeCity/selectCity.vue';
export default {
  data: function data() {
    return {
      hotCity: [],
      recentCity: []
    };
  },
  created: function created() {
    var _this = this;

    api.getHotCity().then(function (res) {
      _this.hotCity = res.data.data;
    });
    api.getRecentCity().then(function (res) {
      _this.recentCity = res.data.data;
    });
  },
  components: {
    mCity: mCity,
    hotCity: hotCity,
    selectCity: selectCity
  }
};