import mSelect from './select.vue';
import api from '@/api/index.js';
export default {
  data: function data() {
    return {
      isDisable: true,
      value: '',
      loading: false,
      isProvince: false,
      isCity: false,
      proName: '省份',
      ciName: "城市",
      options: ['山东', '甘肃', '江苏', '北京', '云南', '海南', '浙江'],
      provinceList: {
        area: '省份',
        arr: []
      },
      cityList: {
        area: '城市',
        arr: []
      }
    };
  },
  created: function created() {
    var _this = this;

    api.getProvinceList().then(function (res) {
      var arr = res.data.data;
      var num = Math.ceil(arr.length / 12);

      for (var i = 0; i < num; i++) {
        _this.provinceList.arr.push(arr.splice(i, 12));
      }
    });
  },
  methods: {
    selProvince: function selProvince(e) {
      e.stopPropagation();
      this.isProvince = true;

      if (this.isCity == true) {
        this.isCity = false;
      }
    },
    selCity: function selCity(e) {
      e.stopPropagation();
      this.isCity = true;

      if (this.isProvince == true) {
        this.isProvince = false;
      }
    },
    documentClick: function documentClick() {
      this.isProvince = false;
      this.isCity = false;
    },
    changePro: function changePro(data) {
      this.proName = data.provinceName;
      this.isDisable = false;
      this.cityList.arr = [];
      var num = Math.ceil(data.cityInfoList.length / 12);

      for (var i = 0; i < num; i++) {
        this.cityList.arr.push(data.cityInfoList.splice(i, 12));
      }

      this.cityList.arr.forEach(function (item) {
        item.forEach(function (ele, index) {
          ele.provinceName = ele.name;
        });
      });
    },
    changeCity: function changeCity(data) {
      this.ciName = data.name;
      this.$store.dispatch('setPosition', data);
      this.$router.push('/');
    },
    remoteMethod: function remoteMethod() {// 请求后端接口
    }
  },
  components: {
    mSelect: mSelect
  }
};